import React from 'react';
import etchasketch from "../images/etch-a-sketch.JPG";
import "../css/Project.css";

class Project extends React.Component {
    render() {
        return (
            <div className="project-container" id="projects">
                <div className="project">
                    <div className="project-content">
                        <h1 className="project-name">Robotic Etch-A-Sketch Project</h1>
                        <h2 className="project-date">April 29, 2020</h2>
                        <p className="project-description">
                            <strong>Purpose:</strong> Senior Design Project for College of Engineering
                            <br/>
                            <br/>
                            <strong>Objective:</strong> To design and fabricate a Robotic Etch-A-Sketch to be used for
                            a lab in an undergraduate Mechatronics course at Penn State. The Etch-A-Sketch was required
                            to involve an Arduino UNO microcontroller and have the capability to draw any JPEG image.
                            <br/>
                            <br/>
                            <strong>Approach:</strong> Our team was split into two groups, three mechanical engineers
                            and three computer engineers. The mechanical engineers focused on designing the frame, which
                            needed to connect motors to the Etch-A-Sketch knobs. The computer engineers focused on
                            developing image processing techniques to break down JPEG images into motor control inputs.
                            <br/>
                            <br/>
                            <strong>My role:</strong> I served as the lead designer of the frame. I designed the
                            couplings which connected the motor shafts to the Etch-A-Sketch knobs. I 3D printed these
                            couplings and manufactured the rest of the frame to ensure stability of the device. I also
                            assisted with motor control functions, as I had previous experience using the Arduino IDE.
                        </p>
                    </div>
                    <img className="project-image" alt="Automated Etch-A-Sketch" src={etchasketch}/>
                </div>

            </div>
        );
    }
}

export default Project;