
import React from 'react';
import josh from "../images/josh2.jpg";
import "../css/Header.css";
import resume from "../JKoch_Resume_Fall2020.pdf";

class Header extends React.Component {
    render() {
        return (
            <div className="header">
                <img src={josh} alt="Josh in front of bay in Nassau" className="header-image" />
                <div className="header-middle">
                    <h1>JOSHUA KOCH</h1>
                    <h2>MECHANICAL ENGINEER</h2>
                    <div className="button-container">
                        <a href={resume} target="_blank" rel="noopener noreferrer" className="header-button">RESUME</a>
                        <a href="mailto:joshua.koch98@gmail.com" className="header-button">EMAIL</a>
                    </div>

                </div>
            </div>
        );
    }
}

export default Header;