import React from 'react';
import josh from '../images/josh_obx_nov2020.jpg';
import "../css/AboutMe.css";

class AboutMe extends React.Component {
    render() {
        return (
            <div className="about-container" id="about-me">
                <div className="about-content">
                    <h1 className="about-name">Hello</h1>
                    <p className="about-description">
                        My name is Josh Koch and I am a recent grad from Penn State University. Prior to obtaining my
                        bachelor's degree in mechanical engineering, I had the opportunity to intern as a a Design
                        Technician and a Manufacturing Engineer. Throughout these experiences and my college career,
                        I gained unique and valuable skills that I use and build upon each day. Through these difficult
                        times, I was fortunately able to secure a job as a contractor for Bayer Pharmaceuticals located
                        in Indianola, PA. I am currently working as a validation engineer, where I validate equipment
                        and processes used for the manufacturing of sterile disposables and other medical devices.
                    </p>
                </div>
                <img className="about-image" alt="Josh with gorgeous evening sky in OBX" src={josh}/>
            </div>
        );
    }
}

export default AboutMe;